/*-------------------------------------------------------*/
table{
    width: 94%;
    border-collapse: collapse;
    background-color: #fff;
    margin-top: 10px;
    border-radius: 8px;
  }
  
  th{
    /* border: 1px solid #e6e6e6; */
    margin: 0;
    width: 25%;
    background-color: #fff;
    font-size: 16px;
    background-image: linear-gradient(#eaf8fa, #60c6d8);
    /* border-radius: 10px; */
  }
  td{
    /* border: 1px solid #d9d9d9; */
    border-left: none;
    font-size: 14px;
  }
  tr:nth-child(2n){
    background-color: #60c6d8;
  }
  tr:nth-child(2n){
    border: none;
  }
  td.increase {
    color:green;
  }
  
  td.decrease {
    color:red;
  }
