.loader-container{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: #60C6D8; */
  background: #ade2eb;
  /* opacity: 0.1; */
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #60C6D8;
  border-radius: 50%;
  width: 130px;
  height: 130px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
